@import '../colors';

.projects-menu, .project-info{
    position: absolute;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;
    .projects-menu-wrapper,.project-info-wrapper{
        position: relative;
        height: 70vh;
        width: 90vw;
        background-color: $yellow;
        border-radius: 2vh;
        box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.1);
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        h2{
            color: $blue;
            font-family: $font2;
            font-size: 4rem;
            text-shadow: 1px 1px rgba(0, 0, 0, 0.5);
            overflow-y: visible;
        }
        h3{
            font-family: $font2;
            font-size: 2.5rem;
            transition: all 200ms ease-in-out;
        }
        h3:hover{
            color: $blue;
        }
        p{
            font-family: $font2;
            font-size:2rem;
            width: 80%;
        }
    }
}

.project-info{
    .project-info-wrapper{
        animation: infoAnim 300ms linear;
        div{
            display: flex;
            flex-direction: column;
            align-items: center;
            p{
                font-family: $font2;
                font-size:2rem;
                width: 80%;
            }
            div{
                display: flex;
                flex-direction: row;
                .git-icon, .website-icon{
                    font-size: 5rem;
                    transition: all 200ms ease;
                }
                .git-icon:hover, .website-icon:hover{
                    cursor: pointer;
                    color: $blue;
                }
            }
        }
    }
}

@keyframes infoAnim {
    0%{
        transform: translateY(60%);
        opacity: 0;
    }
    100%{
        transform: translateX(0);
        opacity: 1;
    }
}

.scene{
    position: fixed;
    width: 100%;
    height: 100vh;
    transition: all 1s ease-in-out;
    .info-btn{
        font-size: 1.8rem;
        color: rgba(0,0,0,0.3);
        transition: all 200ms ease;
        padding-left: 5px;
    }
    .info-btn:hover{
        cursor: pointer;
        color: black;
    }
}

.show-sky-switch{
    position: absolute;
    font-family: $font2;
    font-size: 1rem;
    z-index: 1;
    top: 3%;
    left: 10%;
    overflow-y: visible;
}

@media(min-width:900px){
    .show-sky-switch{
        top: 3%;
        left: 5%;
    }
}


.close:hover{
    cursor: pointer;
}