project-info .project-info-wrapper div div{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row}.project-info .project-info-wrapper div div .git-icon,.project-info .project-info-wrapper div div .website-icon{font-size:5rem;transition:all 200ms ease}.project-info .project-info-wrapper div div .git-icon:hover,.project-info .project-info-wrapper div div .website-icon:hover{cursor:pointer;color:#00b8cd}@-webkit-keyframes infoAnim{0%{-webkit-transform:translateY(60%);transform:translateY(60%);opacity:0}100%{-webkit-transform:translateX(0);transform:translateX(0);opacity:1}}@keyframes infoAnim{0%{-webkit-transform:translateY(60%);transform:translateY(60%);opacity:0}100%{-webkit-transform:translateX(0);transform:translateX(0);opacity:1}}.scene{position:fixed;width:100%;height:100vh;transition:all 1s ease-in-out}.scene .info-btn{font-size:1.8rem;color:rgba(0,0,0,.3);transition:all 200ms ease;padding-left:5px}.scene .info-btn:hover{cursor:pointer;color:#000}.show-sky-switch{position:absolute;font-family:"Fredoka One",cursive;font-size:1rem;z-index:1;top:3%;left:10%;overflow-y:visible}@media(min-width: 900px){.show-sky-switch{top:3%;left:5%}}.close:hover{cursor:pointer}
