@import '../colors';

nav{
    position: fixed;
    z-index: 5;
    background-color: $blue;
    box-shadow: 0 0 2px 2px rgba(0,0,0, 0.1);
    right: 0;
    top: 0;
    height: 10vh;
    width: 10vh;
    display: grid;
    place-items: center;

    .burger-wrapper{
        position: fixed;
        right: 2.5vh;
        top: 2.5vh;
        height: 10vh;
        width: 10vh;
        display: grid;
        place-items: center;
    }
    
    .burger-container{
        z-index: 1;
        overflow: visible;
        position: relative;
        cursor: pointer;
    }
    
    .burger-1, .burger-2, .burger-3{
        margin-bottom: 6px;
        width: 30px;
        height: 6px;
        border-radius:6px;
        background-color: black;
        opacity: 1;
        cursor: pointer;
        transition: transform 300ms linear, opacity 300ms linear;
    }
    
    .burger-3{
        margin-bottom: 0;
    }

    li{
        width: 80%;
        list-style: none;
        font-family: $font1;
        font-weight: 700;
        font-size: 2rem;
        transition: all 200ms ease-in-out;
        .selected{
            color: $yellow;
        }
    }
    
    li:hover{
        cursor: pointer;
        font-size: 2.5rem;
    }

    ul{
        overflow: hidden;
        height: 80%;
        width: 100%;
        visibility: hidden;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    }
}

.show-nav .burger-1{
    position: absolute;
    left: 0;
    top: 50%;
    transform: rotate(45deg);
}

.show-nav .burger-2{
    opacity: 0;
    visibility: hidden;
}

.show-nav .burger-3{
    position: absolute;
    right: 0;
    top: 50%;
    transform: rotate(-45deg);
}

.show-nav ul{
    visibility: visible;
}

canvas{
    z-index: 0;
}